body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, var(--font-family);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
} */

.p-screen {
    padding-left: 2%;
    padding-right: 2%;
}

.container {
    text-align: center;
    max-height: 96.3vh;
}

.act-btn-w {
    height: 50px;
    width: 90%;
    /* border-radius: 6px; */
    /* background-color: #0082f0; */
    /* color: white; */
    /* font-size: 16px; */
    /* border: none; */
    /* font-weight: 520; */
}

.group-btn {
    width: 100%;
    margin-bottom: 5%;
}

.act-btn-back {
    height: 50px;
    width: 90%;
    border-radius: 6px;
    color: black;
    font-size: 16px;
    border: black 1px solid;
    background-color: white;
    font-weight: 520;
    text-align: center;
    text-decoration: none;
    margin-bottom: 15px;
}

.act-btn-w:hover {
    background-color: #8ec8f6;
}

.act-btn-back:hover {
    background-color: #f2f2f2;
}

.act-tf-w {
    width: 90%;
    font-size: 0.95em;
}

.m-top {
    margin-top: 100px;
}
.act-img {
    text-align: center;
    align-items: center;
    justify-content: center;
}

.jc-center {
    justify-content: center;
}

.act-txt {
    font-weight: 700;
    font-size: 24px;
    width: 90%;
    text-align: center;
}

.act-pls {
    text-align: left;
    width: 90%;
    font-weight: 500;
    font-size: 3.5vw;
    margin-bottom: 18px;
}

.err-screen {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    align-items: center;
}

.err-txt {
    font-weight: 700;
    font-size: 6vw;
    color: red;
    max-width: 90%;
    overflow-wrap: break-word;
}

.act-terms {
    width: 90%;
    text-align: center;
    white-space: pre-line;
    white-space: pre-wrap;
    font-size: 18px;
    margin-bottom: 150px;
}

.h-100vh {
    height: 99vh;
    max-height: 100vh;
    width: 100%;
}

.h-10vh {
    max-height: 5vh;
}

.h-90vh {
    height: 94.9vh;
    max-height: 94.9vh;
}

.h-4vh {
    height: 3.6vh;
    max-height: 3.6vh;
}

.p-horizontal-5 {
    width: 100%;
    padding-left: 5%;
    padding-right: 5%;
}

.alg-center {
    align-items: center;
}

.p-back {
    width: 100%;
    padding-left: 5%;
    padding-top: 5%;
}

.t-centext {
    width: 100%;
    text-align: center;
}

.logo-vnpt {
    text-align: center;
    border: #f2f2f2 1px solid;
    max-height: 5vh;
    height: 5vh;
}

.logo-telsoft {
    text-align: center;
    border: #f2f2f2 1px solid;
    max-height: 5vh;
}

.dv-page {
    height: 94.9vh;
    background-color: #f2f2f6;
}

.dv-padding {
    padding: 5% 5% 5% 5%;
}

.dv-text {
    font-weight: 600;
    margin-bottom: 120px;
}

.dv-device {
    background-color: white;
    border-radius: 8px;
}

.dv-flex {
    display: flex;
    height: 100%;
}

.dv-img-device {
    width: 250px;
}

.dv-img {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.dv-img-device {
    margin-top: 13px;
    height: 40px;
    width: 40px;
    background-color: #f2f2f6;
    padding: 10px;
    border-radius: 100%;
    text-align: center;
}

.dv-info {
    width: 90%;
    padding: 10px;
}

.dv-name {
    display: flex;
}

.dv-eid {
    display: flex;
    font-size: 11px;
    margin-top: 20px;
}

.dv-eid-label {
    width: 100%;
}

.dv-eid-value {
    width: 100%;
    text-align: right;
}

.dv-date {
    display: flex;
    font-size: 11px;
    margin-top: 5px;
}

.dv-images {
    width: 20%;
    padding: 10px;
}

.dv-name-value {
    width: 50%;
    font-weight: 600;
}

.dv-name-status {
    text-align: center;
}

.dv-name-cnt {
    width: 100%;
    margin-bottom: 10px;
    font-weight: 500;
}

.dv-btn {
    width: 10%;
    text-align: right;
}

.dv-active {
    font-size: 13px;
    background-color: #ebfaf3;
    border-radius: 8px;
    color: #85e9ca;
    padding: 3px;
    white-space: nowrap;
}

.dv-inactive {
    font-size: 13px;
    background-color: #f2f2f2;
    border-radius: 8px;
    padding: 3px;
    white-space: nowrap;
}

.dv-inprogress {
    font-size: 13px;
    background-color: #f6f2e6;
    border-radius: 8px;
    color: #c6b46c;
    padding: 3px;
    white-space: nowrap;
}

.dv-btn-edit {
    border: none;
    background: none;
    border-radius: 100%;
}

.de-status {
    text-align: center;
    align-self: center;
}

.de-device {
    margin-top: 30px;
    background-color: white;
    border-radius: 10px;
    padding: 20px 10px 20px 10px;
    height: 100%;
}

.de-eid-label {
    font-size: 11px;
    width: 100%;
}

.de-eid-value {
    font-size: 11px;
    width: 100%;
    text-align: right;
}

.de-eid {
    display: flex;
}

.btn-remove {
    background-color: #ed0e00;
    width: 100%;
    height: 42px;
    border: none;
    color: white;
    border-radius: 5px;
}

.dr-btn-remove {
    margin-top: 100px;
    background-color: #ed0e00;
    width: 90%;
    height: 45px;
    border: none;
    color: white;
    border-radius: 5px;
}

.loader-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #f8f8f8ad;
    text-align: center;
}

.loader {
    left: 49%;
    top: 40%;
    z-index: 1000;
    position: absolute;
}

.btn-custom {
    width: 90%;
    border-radius: 6px;
    color: black;
    font-size: 16px;
    border: black 1px solid;
    background-color: white;
    font-weight: 520;
    text-align: center;
    text-decoration: none;
    padding-top: 12px;
    padding-bottom: 12px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.btn-custom:hover {
    background-color: #e7f3fd;
}

.w-80 {
    padding-top: 20px;
    padding-right: 5%;
    padding-left: 5%;
    font-size: 13px;
}

.button-contents {
    display: contents;
}

.img-header {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.btn-fixed {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: grid;
    justify-items: center;
    padding-top: 10px;
    box-shadow: 0 -10px 20px -5px rgb(255 255 255);
    background: #ffffff;
}
.btn-condition {
    height: 50px;
    width: 90%;
    border-radius: 6px;
    background-color: #0082f0;
    color: white;
    font-size: 16px;
    border: none;
    font-weight: 520;
}

.MuiInputLabel-root {
    font-size: 0.9rem !important;
}
.MuiInputLabel-outlined.MuiInputLabel-shrink {
    font-size: 1rem !important;
}

.text-left {
    text-align: left !important;
}

@media only screen and (max-width: 768px) {
    .loader {
        left: 44%;
        top: 40%;
        z-index: 1000;
        position: absolute;
    }
}
